.chat-container {
  max-width: 600px; /* 노트북 기본: 카드형 */
  margin: 20px auto;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 10px;
  background: #f9f9f9;
  display: flex;
  flex-direction: column;
  height: auto; /* 기본은 내용에 맞게 높이 */
}

.chat-messages {
  height: 60vh; /* PC에서는 60% 높이 */
  overflow-y: auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.chat-input {
  display: flex;
  margin-top: 10px;
}
.chat-container h2 {
  margin: 0;
  padding: 10px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

.chat-container hr {
  margin: 10px 0;
  border: none;
  border-top: 1px solid #ccc;
}

.username-input {
  width: 10%;
  height: 3px;
  padding: 14px;
  font-size: 16px;
  margin-bottom: 10px;
  border-radius: 15px;
  border: 1px solid #aaa;
  text-align: center;
}

.chat-messages {
  height: 60vh;
  overflow-y: auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.message {
  display: flex;
  justify-content: flex-start; /* 항상 왼쪽 정렬 */
  margin: 5px 0;
}

.bubble {
  padding: 10px;
  border-radius: 10px;
  background: #fff;
  border: 1px solid #ddd;
  max-width: 90%;
  width: 80%;
  word-wrap: break-word;
}

.sender-name {
  font-size: 13px;
  font-weight: bold;
  margin-bottom: 3px;
  color: #333;
}

.content-line {
  display: flex;
  align-items: center;
}

.content-line span {
  margin: 0;
  flex: 1;
  border-radius: 15px;
  border: solid 1px #838383;
  padding: 0 8px;
}

.delete-btn {
  margin-left: 8px;
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 14px;
  color: #888;
}

.delete-btn:hover {
  color: red;
}

.chat-input {
  display: flex;
  margin-top: 10px;
}

.chat-input input {
  flex: 1;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #aaa;
  font-size: 15px;
}

.chat-input button {
  margin-left: 5px;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background: #4caf50;
  color: white;
  cursor: pointer;
}

/* ========================= */
/* 모바일 전용 (화면 <= 768px) */
/* ========================= */
@media (max-width: 768px) {
  .chat-container {
    width: 100%; /* 가로 전체 차지 */
    height: 100vh; /* 세로 화면 전체 차지 */
    margin: 0; /* 여백 제거 */
    border: none; /* 테두리 제거 */
    border-radius: 0; /* 둥근 모서리 제거 */
    background: #f9f9f9;
    display: flex;
    flex-direction: column;
  }

  .chat-messages {
    flex: 1; /* 남은 공간 전부 채우기 */
    overflow-y: auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
  }

  .chat-input {
    display: flex;
    padding: 10px;
    border-top: 1px solid #ccc;
    background: #fff;
  }
}
